@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@100..900&display=swap');
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Lexend';
  }

:root {
    --mainColor: #151e41;
    --mainColorLight: #3f496e;
    --textColor: #eee;
}

header {
  display: flex;
  align-items:center;
  justify-content:space-between;
  height:100%;
  padding: 0 2rem;
  background-color: var(--mainColor);
  color: var(--textColor);
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 96px;
  padding-right: 20px;
}

.menu-icon {
  font-size: 24px;
  cursor: pointer;
  display: none; 
}

nav a{
    margin: 0 2rem;
    color: var(--textColor);
    text-decoration: none;
    padding: 15px;
    transition: 0.3s ease-in-out;
    font-size:1.3rem;
    font-weight: 400;
}

nav a:hover {
    color:cornflowerblue;
    font-size: 1.4rem;
}

@media (max-width: 768px) {
    .menu-icon {
      display: block;
  }

  .nav-links {
      position: absolute;
      right: 0;
      top: 96px;
      flex-direction: column;
      width: 100%;
      display: none; 
      background: #192F66;
      padding: 20px;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  }

  .nav-links.mobile {
      display: flex; 
  }

  .nav-links a {
      margin: 10px 0;
  }
}

.show-menu {
  display: flex;
}